@import '../constants/index.scss';

.carousel1 {
  padding-top: 20px;
  padding-bottom: 20px;

  @media only screen and (min-width: ($media-screen-tablet + 1)) {
    padding-top: 17px;
    padding-bottom: 17px;
  }

  &__item {
    height: 54px;
    display: flex;
    align-items: center;
    justify-content: center;


    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      height: 114px;
    }

    >div {
      max-width: 65px;

      @media only screen and (min-width: ($media-screen-tablet + 1)) {
        max-width: 178px;
      }
    }


  }
}