@import '../../constants/index.scss';

.tab1 {
  padding-top: 40px;
  padding-bottom: 40px;

  @media only screen and (min-width: ($media-screen-tablet + 1)) {
    padding-top: 80px;
  }

  &__title {
    font-size: 20px;
    font-weight: 700;
    line-height: 28px;
    margin: 0 0 14px 0;
  }

  &__contronler {
    gap: 12px;
    display: flex;
    margin-bottom: 14px;

    &.mini {
      gap: 8px;
    }
  }
}