@import '../../constants/index.scss';

.two-shell {
  display: grid;
  text-align: center;
  padding: 24px 20px 0 24px;
  border-radius: $corner-c2;
  grid-template-columns: repeat(1, minmax(0, 1fr));

  @media only screen and (min-width: ($media-screen-tablet + 1)) {
    padding-top: 100px;
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }

  &__item {
    display: flex;
    margin: 0 auto;
    max-width: 291px;
    align-items: center;
    flex-direction: column;
    justify-content: center;


    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      width: 484px;
      max-width: 92%;
      margin-right: 0;
      align-items: start;
      justify-content: left;

      &:last-child {
        
      }
    }
  }

  &__title {
    font-size: 18px;
    font-weight: 600;
    line-height: 24px;
    margin: 0 0 8px 0;

    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      font-size: 28px;
      font-weight: 700;
      line-height: 36px;
      margin-bottom: 12px;
    }
  }

  &__subtitle {
    font-size: 12px;
    font-weight: 400;
    line-height: 20px;
    margin-bottom: 16px;

    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      font-size: 16px;
      line-height: 24px;
      margin-bottom: 24px;
    }
  }

  &__list {
    padding: 0;
    width: 100%;
    list-style: none;
    margin: 0 0 35px 0;

    li {
      margin-bottom: 16px;

      @media only screen and (min-width: ($media-screen-tablet + 1)) {
        text-align: left;
        margin-bottom: 24px;
      }

      svg {
        min-width: 15px;
        max-width: 15px;
        margin-right: 8px;
        vertical-align: middle;

        @media only screen and (min-width: ($media-screen-tablet + 1)) {
          min-width: 20px;
          max-width: 20px;
        }
      }

      span {
        font-size: 14px;
        font-weight: 400;
        line-height: 22px;
        letter-spacing: 0px;

        @media only screen and (min-width: ($media-screen-tablet + 1)) {
          font-size: 18px;
          line-height: 28px;
        }
      }
    }
  }

  &__crop {
    width: 236px;
    height: 340px;
    overflow: hidden;

    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      width: 337px;
      height: 430px;
    }

    img {
      object-position: top;
    }
  }
}