@import '../../constants/index.scss';

.four-shell {
  padding-top: 80px;
  padding-bottom: 40px;

  @media only screen and (min-width: ($media-screen-mobile + 1)) {
    padding-top: 80px;
    padding-bottom: 40px;
  }

  @media only screen and (min-width: ($media-screen-tablet + 1)) {
    padding-top: 160px;
    padding-bottom: 100px;
  }

  &__title {
    font-size: 20px;
    font-weight: 700;
    line-height: 28px;
    margin: 0 0 40px 0;

    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      font-size: 28px;
      font-weight: 700;
      line-height: 36px;
    }
  }

  &__content {
    gap: 24px;
    display: grid;
    max-width: 852px;
    margin: 0 auto 40px auto;
    grid-template-columns: repeat(1, minmax(0, 1fr));

    @media only screen and (min-width: ($media-screen-mobile + 1)) {
      gap: 24px;
      grid-template-columns: repeat(2, minmax(0, 1fr));
    }

    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      gap: 40px;
      margin-bottom: 0;
      grid-template-columns: repeat(2, minmax(0, 1fr));
    }
  }

  &__icon {
    width: 40px;
    height: 40px;
    display: flex;
    border-radius: 40px;
    align-items: center;
    justify-content: center;
    margin: 0 auto 12px auto;
    color: $color-primary-content;
    background-color: $color-primary;
  }

  &__subtitle {
    font-size: 16px;
    font-weight: 600;
    line-height: 20px;
    margin: 0 0 8px 0;

    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      font-size: 20px;
      font-weight: 700;
      line-height: 28px;
    }
  }

  &__subcontent {
    font-size: 14px;
    font-weight: 400;
    line-height: 22px;
    letter-spacing: 0px;

    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      font-size: 16px;
      line-height: 24px;
    }
  }

  .site-btn {
    display: flex;
    justify-content: center;


    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      display: none;
    }
  }
}