@import '../../constants/index.scss';

.gradian-btn {
  gap: 6px;
  border: none;
  height: 40px;
  display: flex;
  padding: 0 14px;
  font-size: 14px;
  font-weight: 600;
  line-height: 18px;
  text-align: center;
  border-radius: 8px;
  align-items: center;
  color: $color-primary-content;
  font-family: "Manrope", sans-serif;
  background: linear-gradient(90deg, $color-secondary 0%, $color-primary 100%);
}

.fundiin-btn {
  gap: 8px;
  height: 41px;
  display: flex;
  padding: 0 12px;
  font-size: 14px;
  font-weight: 600;
  line-height: 18px;
  text-align: center;
  border-radius: 6px;
  align-items: center;
  color: $color-primary-text;
  background-color: transparent;
  border: 1px solid $color-grey-200;
  font-family: "Manrope", sans-serif;

  &.active {
    color: $color-primary;
    border-color: $color-primary;
  }
  
  &.medium {
    height: 36px;
    padding: 0 10px;

    svg {
      width: 20px;
      height: 20px;
    }
  }

  &.mini {
    height: 32px;
    padding: 0 8px;
  }
}