@import '../constants/index.scss';

.hero {
  height: 680px;
  position: relative;

  @media only screen and (min-width: ($media-screen-tablet + 1)) {
    height: 809px;
  }

  &__content,
  &__filter1,
  &__filter2,
  &__filter3 {
    inset: 0;
    position: absolute;
  }

  &__filter1 {
    background: linear-gradient(180deg, $color-primary 0%, rgba(0, 217, 202, 0) 67.5%);
  }

  &__filter2 {
    background: linear-gradient(90deg, $color-secondary 0%, rgba(109, 95, 240, 0) 100%);
  }

  &__filter3 {
    background: linear-gradient(360deg, #FFFFFF 0%, rgba(255, 255, 255, 0) 68.41%);
  }

  &__content {
    top: 144px;
    display: flex;

    @media only screen and (min-width: ($media-screen-mobile + 1)) {
      top: 275px;
    }

    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      top: 370px;
    }

    h1 {
      margin: 0;
      font-size: 28px;
      font-weight: 700;
      line-height: 36px;
      color: $color-primary-content;

      @media only screen and (min-width: ($media-screen-tablet + 1)) {
        font-size: 44px;
        font-weight: 700;
        line-height: 52px;
      }
    }
  }
}