@import 'constants/index.scss';

.container {
  margin: 0 auto;
  max-width: 335px;
  padding-left: 20px;
  padding-right: 20px;

  @media only screen and (min-width: ($media-screen-mobile + 1)) {
    max-width: 706px;
  }

  @media only screen and (min-width: ($media-screen-tablet + 1)) {
    max-width: 1200px;
  }
}

img,
svg {
  max-width: 100%;
  max-height: 100%;
}

a {
  text-decoration: none;
}

button {
  cursor: pointer;
}

p {
  margin: 0;
  letter-spacing: 0;
}

.cover-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.color-primary {
  color: $color-primary;
}

.color-secondary {
  color: $color-secondary;
}

.color-grey-1000 {
  color: $color-grey-1000
}

.color-grey-600 {
  color: $color-grey-600
}

.color-primary-text {
  color: $color-primary-text
}

.color-secondary-text {
  color: $color-secondary-text
}

.color-primary-content {
  color: $color-primary-content
}

.bg-primary {
  background-color: $color-primary;
}

.bg-secondary {
  background-color: $color-secondary;
}

.bg-grey-1000 {
  background-color: $color-grey-1000
}

.bg-grey-600 {
  background-color: $color-grey-600
}

.bg-primary-text {
  background-color: $color-primary-text
}

.bg-secondary-text {
  background-color: $color-secondary-text
}

.text-center {
  text-align: center;
}

.pointer {
  cursor: pointer;
}