@import '../../constants/index.scss';

.tab2 {
  max-width: 410px;
  text-align: left;

  &__content {
    position: relative;
  }

  &__item {
    min-height: 28px;
    position: relative;
    padding: 16px 40px 16px 60px;


    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      padding-right: 20px;
    }

    &:not(:last-child)::after {
      left: 27px;
      width: 2px;
      content: '';
      bottom: -16px;
      position: absolute;
      background-color: $color-primary;


      @media only screen and (min-width: ($media-screen-mobile + 1)) {
        top: 38px;
      }

      @media only screen and (min-width: ($media-screen-tablet + 1)) {
        top: 48px;
      }
    }
  }

  &__index {
    top: 16px;
    display: flex;
    font-weight: 700;
    position: absolute;
    align-items: center;
    color: $color-primary;
    justify-content: center;
    border: 3px solid $color-primary;

    @media only screen and (min-width: ($media-screen-mobile + 1)) {
      left: 16px;
      width: 17px;
      height: 17px;
      font-size: 12px;
      border-radius: 17px;
    }

    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      left: 12px;
      width: 26px;
      height: 26px;
      font-size: 16px;
      border-radius: 26px;
    }

    &.active {
      color: $color-primary-content;
      background-color: $color-primary;
    }
  }

  &__title {
    margin: 0;

    @media only screen and (min-width: ($media-screen-mobile + 1)) {
      font-size: 16px;
      font-weight: 600;
      line-height: 20px;
    }

    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      font-size: 20px;
      font-weight: 700;
      line-height: 28px;
    }
  }

  &__text {
    margin-top: 4px;

    ul {
      padding-left: 24px;

      li {
        font-size: 16px;
        font-weight: 400;
        line-height: 24px;
        color: $color-secondary-text;

        &::marker {
          color: $color-secondary-text
        }
      }
    }

    p {
      color: $color-secondary-text;

      @media only screen and (min-width: ($media-screen-mobile + 1)) {
        font-size: 14px;
        font-weight: 400;
        line-height: 22px;
      }

      @media only screen and (min-width: ($media-screen-tablet + 1)) {
        font-size: 18px;
        font-weight: 400;
        line-height: 28px;
      }
    }
  }
}