@import '../constants/index.scss';

.header {
  top: 0;
  left: 0;
  right: 0;
  z-index: 9;
  padding-top: 12px;
  position: absolute;

  @media only screen and (min-width: ($media-screen-tablet + 1)) {
    padding-top: 24px;
  }

  &.active {
    bottom: 0;
    background-color: rgb(0 0 0 / 20%);
  }

  &__container {
    height: 56px;
    display: flex;
    padding: 0 24px;
    transition: 0.02s;
    position: relative;
    align-items: center;
    transition-delay: 0.3s;
    border-radius: $corner-c3;
    justify-content: space-between;
    background-color: $color-primary-content;

    &.active {
      transition: 0s;
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;

      .nav {
        padding-bottom: 12px;
      }
    }

    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      height: 88px;
      border-radius: $corner-c2;
    }
  }

  .menu {
    gap: 40px;
    display: flex;
    align-items: center;

    .logo {
      display: flex;
      align-items: center;
    }

    ul {
      left: 0;
      right: 0;
      margin: 0;
      top: 100%;
      padding: 0;
      display: block;
      transition: 0.3s;
      overflow: hidden;
      position: absolute;
      background-color: #fff;
      border-bottom-left-radius: $corner-c3;
      border-bottom-right-radius: $corner-c3;

      @media only screen and (min-width: ($media-screen-tablet + 1)) {
        gap: 80px;
        display: flex;
        position: unset;
        padding-bottom: 0;
        align-items: center;
        border-bottom-left-radius: 0;
        border-bottom-right-radius: 0;
        justify-content: space-between;
      }

      li {
        height: 42px;
        display: flex;
        margin: 0 12px;
        padding: 0 12px;
        list-style: none;
        font-weight: 600;
        align-items: center;

        @media only screen and (min-width: ($media-screen-tablet + 1)) {
          margin: 0;
          padding: 0;
          height: auto;
        }
      }
    }
  }

  .register-btn {
    display: none;

    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      display: flex;
    }
  }

  .collapse-menu-btn {
    padding: 0;
    width: 32px;
    height: 32px;
    border: none;
    display: block;
    border-radius: 50%;

    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      display: none;
    }
  }
}