@import '../../constants/index.scss';

.two-shell2 {
  display: grid;
  margin: 0 auto;
  text-align: center;
  padding: 24px 20px 0 24px;
  border-radius: $corner-c2;
  grid-template-columns: repeat(1, minmax(0, 1fr));

  @media only screen and (min-width: ($media-screen-mobile + 1)) {
    padding: 65.25px 0;
    grid-template-columns: repeat(51, minmax(0, 1fr));
  }

  @media only screen and (min-width: ($media-screen-tablet + 1)) {
    padding-top: 24px;
    grid-template-columns: repeat(50, minmax(0, 1fr));
  }

  &__item {
    @media only screen and (min-width: ($media-screen-mobile + 1)) {
      &:first-child {
        grid-column: span 24/span 24;
      }

      &:last-child {
        grid-column: span 27/span 27;
      }
    }

    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      &:first-child {
        grid-column: span 26/span 26;
      }

      &:last-child {
        grid-column: span 24/span 24;
      }
    }

    .tab1 {
      @media only screen and (min-width: ($media-screen-mobile + 1)) {
        padding-top: 14.75px;
      }

      @media only screen and (min-width: ($media-screen-tablet + 1)) {
        padding-top: 40px;
      }
    }
  }

  &__crop {
    overflow: visible;
    position: relative;

    @media only screen and (min-width: ($media-screen-mobile + 1)) {
      height: 412px;
    }

    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      height: 550px;
    }

    img {
      top: 0;
      bottom: 0;
      max-width: unset;
      position: absolute;
      width: max-content;

      @media only screen and (min-width: ($media-screen-mobile + 1)) {
        left: -50%;
        transform: translate(22%, 0px);
      }

      @media only screen and (min-width: ($media-screen-tablet + 1)) {
        right: 0;
        left: unset;
        transform: translate(1%, 0px);
      }
    }
  }
}