@import '../constants/index.scss';

.direction {

  &--right {
    max-width: 370px;

    @media only screen and (min-width: ($media-screen-mobile + 1)) {
      margin-left: auto;
    }


    @media only screen and (min-width: ($media-screen-tablet + 1)) {
      margin-left: unset;
      margin-right: auto;
    }

    &__pay {
      padding-top: 15px;

      @media only screen and (min-width: ($media-screen-tablet + 1)) {
        padding-top: 80px;
      }

      .tab2 {
        &__item {
          padding-right: 20px;
        }
      }
    }
  }
}